<template>
  <div id="layout-panel">
    <div style="height: 30px; overflow-x: hidden; white-space: nowrap">
      <span
        v-if="!showTextOnly"
        class="inline-span"
        :style="`position:absolute; left: ${
          this.margins.id + this.defaultTreeIndent / 4
        }px`"
        ><strong>{{ $t('Id') }}</strong></span
      >
      <span
        v-if="!showTextOnly"
        class="inline-span"
        :style="`position:absolute; left: ${
          this.margins.readingOrder + this.defaultTreeIndent / 4
        }px`"
        ><strong>{{ $t('Order') }}</strong></span
      >
      <span
        v-if="!showTextOnly"
        class="inline-span"
        :style="`position:absolute; left: ${
          this.margins.structureType + this.defaultTreeIndent / 4
        }px`"
        ><strong>{{ $t('Structure Type') }}</strong></span
      >
      <span
        class="inline-span"
        :style="`position:absolute; left: ${
          (this.showTextOnly ? this.margins.id : this.margins.text) +
          this.defaultTreeIndent / 4
        }px`"
        ><strong>{{ $t('Text') }}</strong></span
      >
    </div>
    <div id="scroll-box" @click="panelClick">
      <Tree
        ref="tree"
        edgeScroll
        :value="treeData"
        :ondragstart="layoutDragStart"
        :ondragend="layoutDragEnd"
      >
        <div
          slot-scope="{ node, path, tree }"
          class="node-container"
          :id="`node_${node.id}`"
          @click="layoutClick(node, path)"
        >
          <span>
            <b
              v-if="node.children && node.children.length > 0"
              @click="tree.toggleFold(node, path)"
            >
              {{ node.$folded ? '+' : '-' }}
            </b>
            <span
              ><strong>{{
                `${node.type === 'Baseline' ? $t('Line') : $t(node.type)} ${
                  node.selected ? '' : ''
                }`
              }}</strong></span
            >
          </span>

          <span
            v-if="!showTextOnly"
            class="column-element"
            :style="getIdStyle(path)"
            ><p>{{ `#${node.id}` }}</p></span
          >
          <span
            v-if="!showTextOnly"
            class="column-element"
            :style="getReadingOrderStyle(path)"
            ><p>{{ path[path.length - 1] + 1 }}</p></span
          >
          <span
            v-if="!showTextOnly"
            class="column-element"
            :style="getStructureStyle(node.structureType, path)"
            ><p>{{ node.structureType }}</p></span
          >

          <span class="column-element" :style="getTextStyle(path)"
            ><p>{{ node.text }}</p></span
          >
        </div>
      </Tree>
    </div>
  </div>
</template>

<script>
import { array_move } from '../geometryFunctions.js'
import { Singleton as Settings } from '../singletons/settings.js'
import 'he-tree-vue/dist/he-tree-vue.css'
import {
  Tree, // Base tree
  Fold,
  Check,
  Draggable, // plugins
  foldAll,
  unfoldAll,
  cloneTreeData,
  walkTreeData,
  getPureTreeData, // utils
} from 'he-tree-vue'
import {
  Singleton as ActionHandler,
  ACTION_TYPE,
  SELECTION_TYPE,
} from '../singletons/actionHandler.js'
import Editor from '../editor.js'
import Baseline from '../shapes/Baseline.js'
import Region from '../shapes/Region.js'
import Table from '../shapes/Table.js'
import { includes } from 'lodash'

export default {
  components: { Tree: Tree.mixPlugins([Fold, Draggable]) },
  props: {
    initData: {
      type: Array,
      default: () => [],
    },
    settings: {
      type: Object,
      default: () => ({}),
    },
    isOpen: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      showTextOnly: false,
      treeData: [],
      activeShapeIds: [],
      controlPressed: false,
      margins: {
        readingOrder: 140,
        structureType: 240,
        text: 340,
        id: 90,
      },
      //https://he-tree-vue.phphe.com/guide.html#ui-structure-and-csss
      defaultTreeIndent: 20,
    }
  },
  watch: {
    settings(newSettings, oldSettings) {
      const hasChanged = (key) => oldSettings?.[key] !== newSettings?.[key]
      const tree = this.$refs.tree
      if (tree == null) return

      if (hasChanged('treeExpand')) {
        if (newSettings.treeExpand) {
          tree.unfoldAll()
        } else {
          tree.foldAll()
        }
      }
      if (hasChanged('treeShowTextOnly')) {
        this.showTextOnly = newSettings.treeShowTextOnly
        // console.log(this.showTextOnly)
      }
    },
    initData(newData) {
      const treeData = JSON.parse(JSON.stringify(newData))
      const tree = this.$refs.tree
      this.extendTree(treeData)
      if (tree) {
        tree.rootNode.$droppable = () => {
          if (this.dragType === 'Baseline') {
            return false
          }
          return true
        }
      }
      if (this.settings.treeExpand) {
        unfoldAll(treeData)
      }
      setTimeout(() => this.scrollContainerByNodeId(this.activeShapeIds[0]), 0)
      this.treeData = treeData
    },
    activeShapeIds(newId, oldId) {
      // if (oldId != null) {
      //   const info = this.getTreeNodeInfo(oldId)
      //   if (info?.node) {
      //     info.node.$nodeStyle = ''
      //     info.node.$nodeBackClass = 'node-hover'
      //     info.node.selected = false
      //   }
      // }
      // if (newId != null) {
      //   const info = this.getTreeNodeInfo(newId)
      //   if (info?.node) {
      //     info.node.$nodeStyle = info.node.styleSelected
      //     info.node.$nodeBackClass = ''
      //     info.node.selected = true
      //     this.unFoldNodePath(newId)
      //     this.$nextTick(() => {
      //       this.scrollContainerByNodeId(newId)
      //     })
      //   }
      // }
    },
  },
  mounted() {
    // console.log('tree mounted')
    document.addEventListener('keydown', (event) => {
      switch (event.key) {
        case 'Control': {
          this.controlPressed = true
        }
      }
    })
    document.addEventListener('keyup', (event) => {
      switch (event.key) {
        case 'Control': {
          this.controlPressed = false
        }
      }
    })
  },
  methods: {
    settingsUpdate(type) {},
    extendTree(tree) {
      walkTreeData(tree, (node) => {
        const { id, children } = node
        const isSelected = (id) => this.activeShapeIds.includes(id)
        const expand = children?.some(
          (c) => isSelected(c.id) || c.children?.some((c2) => isSelected(c2.id))
        )
        //console.log(expand)
        this.addProperties(node, expand, isSelected(id))
        // console.log(node)
      })
    },
    unFoldNodePath(id) {
      // console.log(id)
      const tree = this.$refs.tree
      const info = this.getTreeNodeInfo(id)
      if (info?.parent) {
        //  console.log('unfold')
        tree.unfold(info.parent)
        this.unFoldNodePath(info.parent.id)
      }
    },
    getTreeNodeInfo(id) {
      let found
      this.$refs.tree.walkTreeData((node, index, parent, path) => {
        if (node.id === id) {
          found = { node, index, parent, path }
          return false
        }
      })
      return found
    },
    getStructureStyle(type, path) {
      const color = Settings.instance().structureTypes.find(
        (t) => t.name === type
      )?.color
      //console.log(path.length)
      const shift = (path.length - 1) * this.defaultTreeIndent
      return `color: ${color}; left: ${this.margins.structureType - shift}px`
    },
    getIdStyle(path) {
      const shift = (path.length - 1) * this.defaultTreeIndent
      return `left: ${this.margins.id - shift}px`
    },
    getReadingOrderStyle(path) {
      const shift = (path.length - 1) * this.defaultTreeIndent
      return `left: ${this.margins.readingOrder - shift}px`
    },
    getTextStyle(path) {
      const shift = (path.length - 1) * this.defaultTreeIndent
      return `left: ${
        (this.showTextOnly ? this.margins.id : this.margins.text) - shift
      }px`
    },
    scrollContainerByNodeId(id) {
      const node = document.querySelector(`#node_${id}`)
      if (node == null) return
      const offset = node.offsetTop
      const container = document.querySelector(`#scroll-box`)
      if (container == null) return
      let halfContainerHeight = container.clientHeight / 2
      // halfContainerHeight = 180
      const newScrollTop = offset - halfContainerHeight
      //checks if node is already visible
      if (Math.abs(container.scrollTop - newScrollTop) < halfContainerHeight) {
        return
      }
      container.scrollTop = newScrollTop
    },
    performSelection(selection) {
      const removeNode = (id) => {
        if (this.activeShapeIds.includes(id) === false) return
        const info = this.getTreeNodeInfo(id)
        if (info?.node) {
          info.node.$nodeStyle = ''
          info.node.$nodeBackClass = 'node-hover'
          info.node.selected = false
          this.activeShapeIds.splice(this.activeShapeIds.indexOf(id), 1)
        }
      }
      const addNode = (id) => {
        if (this.activeShapeIds.includes(id)) return
        const info = this.getTreeNodeInfo(id)
        if (info?.node) {
          info.node.$nodeStyle = info.node.styleSelected
          info.node.$nodeBackClass = ''
          info.node.selected = true
          this.activeShapeIds.push(id)
        }
      }

      switch (selection.type) {
        case SELECTION_TYPE.REMOVE: {
          const newIds = selection.ids
          newIds.map((id) => {
            removeNode(id)
          })
          break
        }
        case SELECTION_TYPE.ADD: {
          const newIds = selection.ids
          newIds.map((id) => {
            addNode(id)
            this.unFoldNodePath(id)
          })
          this.$nextTick(() => {
            this.scrollContainerByNodeId(newIds[0])
          })
          break
        }
        case SELECTION_TYPE.CLEAR: {
          const ids = [...this.activeShapeIds]
          ids.map((id) => removeNode(id))
          break
        }
      }
    },
    performAction(action) {
      if (action == null) return
      const tree = this.$refs.tree
      switch (action.type) {
        case ACTION_TYPE.MULTI_ACTION:
          action.actions.map((action) => {
            this.performAction(action)
          })
          break
        case ACTION_TYPE.ADD_SHAPE: {
          const info = this.getTreeNodeInfo(action.parent_id)
          if (action.shape instanceof Baseline) {
            const parent = info?.node
            if (parent == null) break
            const subTree = action.shape.getLayout()
            this.extendTree(subTree)
            parent.children.splice(action.index, 0, subTree)
            this.unFoldNodePath(action.shape.id)
            break
          }
          if (action.shape instanceof Region) {
            const subTree = action.shape.getLayout()
            this.extendTree(subTree)
            tree.rootNode.children.splice(action.index, 0, subTree)
            break
          }
          if (action.shape instanceof Table) {
            const subTree = action.shape.getLayout()
            this.extendTree(subTree)
            tree.rootNode.children.splice(action.index, 0, subTree)
            break
          }
          break
        }
        case ACTION_TYPE.REMOVE_SHAPE: {
          const info = this.getTreeNodeInfo(action.shape.id)
          if (info == null) break
          const parent = info.parent
          if (action.shape instanceof Baseline) {
            parent.children.splice(action.index, 1)
          }
          if (action.shape instanceof Region || action.shape instanceof Table) {
            tree.rootNode.children.splice(action.index, 1)
          }
          break
        }
        case ACTION_TYPE.CHANGE_READING_ORDER: {
          const info = this.getTreeNodeInfo(action.shape_id)
          if (info == null) break
          const { node, parent } = info
          if (node.type === 'Region' || node.type === 'Table') {
            array_move(
              tree.rootNode.children,
              action.old_index,
              action.new_index
            )
          }
          if (node.type === 'Baseline') {
            array_move(parent.children, action.old_index, action.new_index)
          }
          break
        }
        case ACTION_TYPE.CHANGE_PARENT: {
          const oldInfo = this.getTreeNodeInfo(action.old_parent_id)
          const newInfo = this.getTreeNodeInfo(action.new_parent_id)
          if (oldInfo == null || newInfo == null) break
          const oldParent = oldInfo.node
          const newParent = newInfo.node
          const movedNodes = oldParent.children.splice(
            action.old_index,
            action.shape_count
          )
          newParent.children.splice(action.new_index, 0, ...movedNodes)
          break
        }
        case ACTION_TYPE.REMOVE_CELL:
        case ACTION_TYPE.ADD_CELL: {
          //the entire table gets redrawn. Not perfect, but a work around
          //Other possible solution: trigger this using the multiAction Object
          //Achtung: this depends on the fact that the editor recieves the action earlier
          const info = this.getTreeNodeInfo(action.shape_id)
          if (info == null) break
          const tableIndex = info?.index
          const subTree = action.table.getLayout()
          this.extendTree(subTree)
          tree.rootNode.children.splice(tableIndex, 1, subTree)
          this.unFoldNodePath(action.cell.id)
          break
        }
        case ACTION_TYPE.SET_STRUCTUR_TYPE: {
          const info = this.getTreeNodeInfo(action.shape_id)
          if (info == null) break
          const { node } = info
          node.structureType = action.new_type
          if (action.new_type === 'none') {
            node.structureType = null
          }
          break
        }
        case ACTION_TYPE.POSITION_MOVE: {
          const info = this.getTreeNodeInfo(action.shape_id)
          if (info == null) break
          const { index } = info
          const parent = info.parent || tree.rootNode
          array_move(parent.children, index, index + action.distance)
          break
        }
      }
    },
    addProperties(node, expand, selected) {
      const type = node.type
      const styleSelected = `background-color: ${
        type === 'Baseline'
          ? 'rgba(0, 0, 255, 0.3)'
          : type === 'Region'
          ? 'rgba(0, 128, 0, 0.3)'
          : type === 'Table'
          ? 'rgba(249, 180, 45, 0.3)'
          : 'rgba(255,255, 0, 0.3)'
      };`
      node.selected = selected
      node.styleSelected = styleSelected
      node.$nodeStyle = selected ? styleSelected : ''
      node.$nodeBackClass = !selected ? 'node-hover' : ''
      node.$folded = !expand
      node.$droppable = () => {
        if (this.dragType === 'Baseline') {
          if (type === 'Region' || type === 'Cell') {
            return true
          }
          return false
        }
        if (this.dragType === 'Region' || this.dragType === 'Table') {
          return false
        }
      }
    },
    selectionAddShape(id) {
      const tree = this.$refs.tree
      ActionHandler.instance().select({
        type: SELECTION_TYPE.ADD,
        ids: [id],
      })
      const { node, path } = this.getTreeNodeInfo(id)
      if (node.type === 'Cell') {
        const parent = tree.getNodeByPath(path.slice(0, -1))
        if (this.activeShapeIds.includes(parent.id) === false) {
          ActionHandler.instance().select({
            type: SELECTION_TYPE.ADD,
            ids: [parent.id],
          })
        }
      }
    },
    selectionRemoveShape(id) {
      const tree = this.$refs.tree
      ActionHandler.instance().select({
        type: SELECTION_TYPE.REMOVE,
        ids: [id],
      })
      const { node, path } = this.getTreeNodeInfo(id)
      if (node.type === 'Table') {
        node.children.map((c) => {
          if (this.activeShapeIds.includes(c.id)) {
            ActionHandler.instance().select({
              type: SELECTION_TYPE.REMOVE,
              ids: [c.id],
            })
          }
        })
      }
    },
    layoutDragStart(tree, { dragNode }) {
      this.dragType = dragNode.type
      if (!this.activeShapeIds.includes(dragNode.id)) {
        ActionHandler.instance().select({
          type: SELECTION_TYPE.CLEAR,
        })
        this.selectionAddShape(dragNode.id)
      }
      if (dragNode.type === 'Cell') {
        return false
      }
      return true
    },
    layoutDragEnd(tree, { startPath, targetPath, dragNode: { id } }) {
      //https://www.tutorialspoint.com/javascript-to-check-consecutive-numbers-in-array#:~:text=To%20check%20for%20consecutive%20numbers,false%20is%20the%20return%20value.
      const sequceIsConsecutive = (array) =>
        array.length <= 1 ||
        Boolean(
          array.reduce((output, lastest) =>
            !isNaN(output) ? (output + 1 === lastest ? lastest : false) : false
          )
        )

      const differentParents = (ids) => {
        let areDifferent = false
        ids.reduce((first, second) => {
          log(first)
          const pPathFirst = this.getTreeNodeInfo(first).path.slice(0, -1)
          const pPathSecond = this.getTreeNodeInfo(second).path.slice(0, -1)
          if (JSON.stringify(pPathFirst) !== JSON.stringify(pPathSecond)) {
            areDifferent = true
          }
          return second
        })
        return areDifferent
      }

      const activeIndices = this.activeShapeIds.map(
        (id) => this.getTreeNodeInfo(id).index
      )
      activeIndices.sort((a, b) => a - b)

      const shapeId = id
      const oldIndex = startPath[startPath.length - 1]
      const parent = tree.getNodeByPath(startPath.slice(0, -1)) || tree.rootNode
      const oldParentId = parent?.id
      let newIndex = targetPath[targetPath.length - 1]
      const newParentId = tree.getNodeByPath(targetPath.slice(0, -1))?.id
      //this is a correction
      if (oldParentId === newParentId && newIndex > oldIndex) {
        newIndex--
      }

      let action = this.getDragAction(
        oldParentId,
        newParentId,
        oldIndex,
        newIndex,
        shapeId
      )

      if (
        this.activeShapeIds.length > 1 &&
        !differentParents(this.activeShapeIds) &&
        sequceIsConsecutive(activeIndices)
      ) {
        let distance = newIndex - oldIndex
        const ids = [...this.activeShapeIds]

        const isTooBig = (ids, distance, parent) =>
          ids
            .map((id) => this.getTreeNodeInfo(id).index)
            .some((index) => index + distance > parent.children.length - 1)

        const isTooSmall = (ids, distance) =>
          ids
            .map((id) => this.getTreeNodeInfo(id).index)
            .some((index) => index + distance < 0)

        const correctDistance = (ids, distance, parent) => {
          if (isTooBig(ids, distance, parent)) {
            return correctDistance(ids, distance - 1, parent)
          }
          if (isTooSmall(ids, distance)) {
            return correctDistance(ids, distance + 1, parent)
          }
          return distance
        }
        if (oldParentId === newParentId) {
          if (distance > 0) {
            ids.sort(
              (a, b) =>
                this.getTreeNodeInfo(b).index - this.getTreeNodeInfo(a).index
            )
          } else {
            ids.sort(
              (a, b) =>
                this.getTreeNodeInfo(a).index - this.getTreeNodeInfo(b).index
            )
          }
          distance = correctDistance(ids, distance, parent)
          action = {
            type: ACTION_TYPE.MULTI_ACTION,
            actions: ids.map((id) => this.getMoveAction(id, distance)),
          }
        } else {
          ids.sort(
            (a, b) =>
              this.getTreeNodeInfo(a).index - this.getTreeNodeInfo(b).index
          )
          action = {
            type: ACTION_TYPE.CHANGE_PARENT,
            shape_count: ids.length,
            old_parent_id: oldParentId,
            new_parent_id: newParentId,
            old_index: this.getTreeNodeInfo(ids[0]).index,
            new_index: newIndex,
          }
        }
      }
      ActionHandler.instance().doAction(action)
      ActionHandler.instance().addAction(action)
      return false
    },
    layoutClick(node) {
      if (!this.controlPressed) {
        ActionHandler.instance().select({
          type: SELECTION_TYPE.CLEAR,
        })
        this.selectionAddShape(node.id)
        return
      }
      if (this.activeShapeIds.includes(node.id)) {
        this.selectionRemoveShape(node.id)
        return
      }
      this.selectionAddShape(node.id)
    },
    panelClick(e) {
      if (e.target !== e.currentTarget) return
      ActionHandler.instance().select({
        type: SELECTION_TYPE.CLEAR,
      })
    },
    getDragAction(oldParentId, newParentId, oldIndex, newIndex, shapeId) {
      if (oldParentId != newParentId) {
        return {
          type: ACTION_TYPE.CHANGE_PARENT,
          shape_count: 1,
          old_parent_id: oldParentId,
          new_parent_id: newParentId,
          old_index: oldIndex,
          new_index: newIndex,
        }
      } else {
        return {
          type: ACTION_TYPE.CHANGE_READING_ORDER,
          shape_id: shapeId,
          old_index: oldIndex,
          new_index: newIndex,
        }
      }
    },
    // getMultiDragAction(oldParentId, newParentId, oldIndex, newIndex, ids) {
    //   if (oldParentId != newParentId) {
    //     return {
    //       type: ACTION_TYPE.MULTI_CHANGE_PARENT,
    //       shape_ids: ids,
    //       old_parent_id: oldParentId,
    //       new_parent_id: newParentId,
    //       old_index: oldIndex,
    //       new_index: newIndex,
    //     }
    //   } else {
    //     return {
    //       type: ACTION_TYPE.MULTI_CHANGE_READING_ORDER,
    //       shape_ids: ids,
    //       old_index: oldIndex,
    //       new_index: newIndex,
    //     }
    //   }
    // },
    getMoveAction(id, distance) {
      return {
        type: ACTION_TYPE.POSITION_MOVE,
        shape_id: id,
        distance: distance,
      }
    },
  },
}
</script>

