<template>
  <div style="width: 100vw; height: 100vh">
    <LayoutEditor
      :pageXml="pageXml"
      :tileSource="tileSource"
      :structureTypes="[
        { name: 'paragraph', color: '#1CE6FF', default: 'true' },
        { name: 'heading', color: '#FF34FF', default: 'true' },
        { name: 'caption', color: '#008941', default: 'true' },
        { name: 'header', color: '#006FA6', default: 'true' },
        { name: 'footer', color: '#A30059', default: 'true' },
        { name: 'page-number', color: '#FFFF00', default: 'true' },
        { name: 'drop-capital', color: '#00846F', default: 'true' },
        { name: 'credit', color: '#FFDBE5', default: 'true' },
        { name: 'floating', color: '#7A4900', default: 'true' },
        { name: 'signature-mark', color: '#0000A6', default: 'true' },
        { name: 'catch-word', color: '#63FFAC', default: 'true' },
        { name: 'marginalia', color: '#B79762', default: 'true' },
        { name: 'footnote', color: '#004D43', default: 'true' },
        { name: 'footnote-continued', color: '#8FB0FF', default: 'true' },
        { name: 'endnote', color: '#997D87', default: 'true' },
        { name: 'TOC-entry', color: '#5A0007', default: 'true' },
      ]"
      @publishXml="save"
    ></LayoutEditor>
  </div>
</template>

<script>
import LayoutEditor from './components/LayoutEditor'
import pageXml from './pageXML'
import tileSource from './tileSource'
export default {
  name: 'App',

  data() {
    return {
      pageXml: pageXml,
      tileSource: tileSource,
    }
  },

  created() {
    const xml = window.layoutEditorConfig?.xml
    const iiifString = window.layoutEditorConfig?.iiifJson
    let iiifJson
    try {
      iiifJson = JSON.parse(iiifString)
    } catch (error) {
      console.log('Error: iiifJson in not in JSON format')
    }

    if (xml) {
      this.pageXml = xml
    }

    if (iiifJson) {
      this.tileSource = iiifJson
    }
  },

  components: {
    LayoutEditor,
  },
  methods: {
    save(xml) {
      if (window.onLayoutSave) {
        window.onLayoutSave(xml)
      }
    },
  },
}
</script>

<style></style>
