<template>
  <div class="modal-card" style="width: auto">
    <header class="modal-card-head">
      <p class="modal-card-title">
        <b-icon :style="{ 'margin-right': '10px' }" icon="cogs"></b-icon>
        {{ $t('Settings') }}
      </p>
      <button type="button" class="delete" @click="$emit('close')" />
    </header>
    <section class="modal-card-body">
      <b-tabs type="is-boxed" position="is-centered" expanded>
        <b-tab-item :label="$t('General')" icon="tune">
          <div class="tab-content">
            <ul>
              <li>
                <b-switch
                  size="is-small"
                  v-model="handles.showRegions"
                  @input="changed('showRegions')"
                >
                </b-switch>
                {{ $t('Show Regions') }}
              </li>
              <li>
                <b-switch
                  size="is-small"
                  v-model="handles.showBaselines"
                  @input="changed('showBaselines')"
                >
                </b-switch>
                {{ $t('Show Baselines') }}
              </li>
              <li>
                <b-switch
                  size="is-small"
                  v-model="handles.showWords"
                  @input="changed('showWords')"
                >
                </b-switch>
                {{ $t('Show Words') }}
              </li>
              <li>
                <b-switch
                  size="is-small"
                  v-model="handles.showStructureTypes"
                  @input="changed('showStructureTypes')"
                >
                </b-switch>
                {{ $t('Show structure types') }}
              </li>
              <li>
                <b-switch
                  size="is-small"
                  v-model="handles.showReadingOrder"
                  @input="changed('showReadingOrder')"
                >
                </b-switch>
                {{ $t('Show readingorder') }}
              </li>
              <li>
                <b-switch
                  size="is-small"
                  v-model="handles.showRelations"
                  @input="changed('showRelations')"
                >
                </b-switch>
                {{ $t('Show relations') }}
              </li>
              <li>
                <div
                  :style="{
                    display: 'flex',
                    'flex-direction': 'horizontal',
                    'align-items': 'center',
                  }"
                >
                  <b-slider
                    :style="{ width: '100px' }"
                    v-model="handles.structureTypeSize"
                    rounded
                    :min="5"
                    :max="80"
                    @input="changed('structureTypeSize')"
                  ></b-slider>
                  <span :style="{ 'margin-left': '20px' }">{{
                    $t('Change label size')
                  }}</span>
                  <div>
                    <b-tooltip position="is-right" :label="$t('Reset')">
                      <b-button
                        icon-left="rotate-left"
                        @click="reset('structureTypeSize')"
                        :style="{
                          'margin-left': '20px',
                          width: '30px',
                          height: '30px',
                        }"
                      >
                      </b-button>
                    </b-tooltip>
                  </div>
                </div>
              </li>
              <li>
                <div
                  :style="{
                    display: 'flex',
                    'flex-direction': 'horizontal',
                    'align-items': 'center',
                  }"
                >
                  <b-slider
                    :style="{ width: '100px' }"
                    v-model="handles.circleSize"
                    rounded
                    :min="2"
                    :max="10"
                    @input="changed('circleSize')"
                  ></b-slider>
                  <span :style="{ 'margin-left': '20px' }">{{
                    $t('Change circle size')
                  }}</span>
                  <div>
                    <b-tooltip position="is-right" :label="$t('Reset')">
                      <b-button
                        icon-left="rotate-left"
                        @click="reset('circleSize')"
                        :style="{
                          'margin-left': '20px',
                          width: '30px',
                          height: '30px',
                        }"
                      >
                      </b-button>
                    </b-tooltip>
                  </div>
                </div>
              </li>
              <li @click="openStructureManager" style="cursor: pointer">
                <b-icon icon="label-multiple" class="media-left" />
                {{ $t('Manage Structure Types') }}
              </li>

              <li @click="openShortcutManager" style="cursor: pointer">
                <b-icon icon="keyboard" class="media-left" />
                {{ $t('Manage keyboard shortcuts') }}
              </li>
            </ul>
          </div>
        </b-tab-item>
        <b-tab-item :label="$t('Layouttree')" icon="file-tree">
          <div class="tab-content">
            <ul>
              <li>
                <b-switch
                  size="is-small"
                  v-model="handles.treeExpand"
                  @input="changed('treeExpand')"
                >
                </b-switch>
                {{ $t('Always expand') }}
              </li>
              <li>
                <b-switch
                  size="is-small"
                  v-model="handles.treeShowTextOnly"
                  @input="changed('treeShowTextOnly')"
                >
                </b-switch>
                {{ $t('Only display text') }}
              </li>
            </ul>
          </div>
        </b-tab-item>
      </b-tabs>
    </section>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import StructureTypeManager from './StructureTypeManager.vue'
import ShortcutManager from './ShortcutManager.vue'
import { Singleton as Settings } from '../singletons/settings.js'

import AddStructureType from './AddStructureType.vue'
import 'vue-swatches/dist/vue-swatches.css'

export default {
  props: {
    structureTypes: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      handles: {},
    }
  },
  mounted() {
    this.fetchSettings()
  },

  methods: {
    fetchSettings() {
      const settings = Settings.instance()
      this.handles = {
        ...settings.getAll(),
      }
    },
    changed(key) {
      Settings.instance().set({ [key]: this.handles[key] })
    },
    reset(key) {
      Settings.instance().reset(key)
      this.fetchSettings()
    },
    openStructureManager() {
      this.$buefy.modal.open({
        parent: this,
        component: StructureTypeManager,
        props: {
          initTypes: this.structureTypes,
        },
        events: {
          save: (structureTypes) => {
            this.$emit('save', structureTypes)
          },
        },
      })
    },
    openShortcutManager() {
      this.$buefy.modal.open({
        parent: this,
        component: ShortcutManager,
      })
    },
  },
}
</script>

