<template>
  <div class="card" style="width: auto">
    <header class="card-header">
      <div class="card-header-title">
        {{ $t('Create new structure type') }}
      </div>
      <div class="card-header-icon">
        <button @click="$parent.close()" class="delete is-large"></button>
      </div>
    </header>
    <div class="card-content">
      <div class="content">
        <b-field label="Name" style="margin-top: 20px">
          <b-input v-model.trim="type.name" @input="format()"></b-input>
        </b-field>
        <b-field
          :label="$t('Choose color')"
          style="margin-top: 40px; margin-bottom: 30px"
        >
          <v-swatches
            v-if="swatchesExists()"
            v-model="type.color"
            fallback-input-type="color"
            swatches="text-advanced"
            show-fallback
            popover-y="top"
          ></v-swatches>
        </b-field>
        <p class="control">
          <b-button
            icon-left="plus"
            @click="create"
            class="button is-primary"
            >{{ $t('Create') }}</b-button
          >
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import 'vue-swatches/dist/vue-swatches.css'

export default {
  data() {
    return {
      type: {
        name: 'newType',
        color: '#00aaff',
      },
    }
  },
  props: {
    currentTypes: {
      type: Array,
      required: true,
      default: () => [],
    },
  },
  methods: {
    swatchesExists() {
      return this.$router != null
    },
    create() {
      if (this.currentTypes.some((type) => type.name === this.type.name)) {
        this.openToast(this.$t('This type already exists'), 'is-danger')
      } else {
        this.$emit('closeModal', this.type)
      }
    },
    format() {
      this.type.name = this.type.name.replaceAll(' ', '-')
    },
    openToast(message, type) {
      this.$buefy.toast.open({
        duration: 4000,
        message: message,
        position: 'is-top',
        type: type,
      })
    },
  },
}
</script>
