import Editor from './editor'
import Relation from './shapes/Relation'

function getStatePropertyWrappers() {
  const addRelation = {}
  // addRelation.resetRelation = () => {
  //   addRelation.state = Editor.STATE.ADD_RELATION
  //   addRelation.isEmpty = true
  //   addRelation.activeArrow = null
  //   addRelation.activeInit = []
  //   addRelation.ids = []
  // }
  addRelation.reset = () => {
    addRelation.state = Editor.STATE.ADD_RELATION
    addRelation.isEmpty = true
    addRelation.draftArrows = []
    addRelation.activeArrow = null
    addRelation.activeInit = []
    addRelation.ids = []
    addRelation.type = Relation.TYPE.LINK
  }
  addRelation.reset()
  return [addRelation]
}

export { getStatePropertyWrappers }
