<template>
  <div
    id="box"
    v-bind:style="{
      'min-width': windowWidth + 'px',
      width: windowWidth + 'px',
    }"
    class="is-relative"
  >
    <!-- <div id="box" class="is-relative"> -->
    <div id="osd" ref="osd" class="is-relative has-background-dark"></div>
    <div id="navigator-overlay">
      <div style="margin-top: 2px">
        <b-tooltip position="is-left" :label="$t('Increase zoom level')">
          <b-button @click="zoomIn">
            <b-icon class="button-icon" icon="plus-box-outline"></b-icon>
          </b-button>
        </b-tooltip>
      </div>
      <div>
        <b-tooltip position="is-left" :label="$t('Decrease zoom level')">
          <b-button @click="zoomOut">
            <b-icon class="button-icon" icon="minus-box-outline"></b-icon>
          </b-button>
        </b-tooltip>
      </div>
      <div>
        <b-tooltip position="is-left" :label="$t('Center')">
          <b-button @click="setHome">
            <b-icon
              class="button-icon"
              icon="image-filter-center-focus"
            ></b-icon>
          </b-button>
        </b-tooltip>
      </div>
      <div>
        <b-tooltip position="is-left" :label="$t('Fit to width')">
          <b-button @click="fitToWidth">
            <b-icon class="button-icon" icon="arrow-left-right"></b-icon>
          </b-button>
        </b-tooltip>
      </div>
      <!-- <div>
        <b-tooltip position="is-left" :label="$t('Fullscreen')">
          <b-button v-fullscreen.teleport="options" @click="fullscreen">
            <b-icon class="button-icon" icon="fullscreen"></b-icon>
          </b-button>
        </b-tooltip>
      </div> -->
    </div>
    <div id="editor-overlay">
      <div style="margin-bottom: 20px">
        <b-tooltip position="is-right" :label="$t('Save')">
          <b-button
            icon-left="content-save"
            @click="save"
            style="background-color: #48c774"
            class="editor-button"
          >
          </b-button>
        </b-tooltip>
      </div>
      <div>
        <b-tooltip position="is-right" :label="$t('Selection Mode')">
          <b-button
            @click="buttonClick(Editor.BUTTON.POINTER)"
            :style="getButtonStyle(Editor.BUTTON.POINTER)"
            class="editor-button"
            id="pointer"
          >
          </b-button>
        </b-tooltip>
      </div>
      <div>
        <b-tooltip position="is-right" :label="$t('Add Line')">
          <b-button
            @click="buttonClick(Editor.BUTTON.ADD_BASELINES)"
            :style="getButtonStyle(Editor.BUTTON.ADD_BASELINES)"
            class="editor-button"
            id="add-textline"
          >
          </b-button>
        </b-tooltip>
      </div>
      <div>
        <b-tooltip position="is-right" :label="$t('Add Region')">
          <b-button
            @click="buttonClick(Editor.BUTTON.ADD_REGIONS)"
            :style="getButtonStyle(Editor.BUTTON.ADD_REGIONS)"
            class="editor-button"
            id="add-textregion"
          >
          </b-button>
        </b-tooltip>
      </div>
      <div>
        <b-tooltip position="is-right" :label="$t('Add Table')">
          <b-button
            @click="buttonClick(Editor.BUTTON.ADD_TABLES)"
            :style="getButtonStyle(Editor.BUTTON.ADD_TABLES)"
            class="editor-button"
            id="add-table"
          >
          </b-button>
        </b-tooltip>
      </div>
      <!-- <div>
        <b-tooltip position="is-right" :label="$t('Add Relation')">
          <b-button
            icon-left="transit-connection-horizontal"
            @click="buttonClick(Editor.BUTTON.ADD_RELATION)"
            :style="getButtonStyle(Editor.BUTTON.ADD_RELATION)"
            class="editor-button"
            id="add-relation"
          >
          </b-button>
        </b-tooltip>
      </div> -->
      <div>
        <b-tooltip position="is-right" :label="$t('Undo')">
          <b-button
            icon-left="undo"
            @click="buttonClick(Editor.BUTTON.UNDO)"
            class="editor-button"
            id="undo-button"
          >
          </b-button>
        </b-tooltip>
      </div>
      <div>
        <b-tooltip position="is-right" :label="$t('Redo')">
          <b-button
            icon-left="redo"
            @click="buttonClick(Editor.BUTTON.REDO)"
            class="editor-button"
            id="redo-button"
          >
          </b-button>
        </b-tooltip>
      </div>
      <div style="margin-top: 20px">
        <b-tooltip position="is-right" :label="$t('References')">
          <b-dropdown position="is-top-right">
            <template #trigger="{}">
              <b-button
                icon-left="message-outline"
                class="editor-button"
                id="shortcut-button"
              >
              </b-button>
            </template>
            <b-dropdown-item :custom="true">
              <div class="description">
                <span :style="{ 'margin-bottom': '10px' }">{{
                  `${$t('References')}:`
                }}</span>
                <ul>
                  <li v-for="shortcut in instructions" :key="shortcut.string">
                    <span v-html="shortcut.string"></span>
                  </li>
                </ul></div
            ></b-dropdown-item>
          </b-dropdown>
        </b-tooltip>
      </div>
      <div>
        <b-tooltip position="is-right" :label="$t('Settings')">
          <b-button
            icon-left="cogs"
            @click="openSettingsModal"
            class="editor-button"
            id="settings-button"
          >
          </b-button>
        </b-tooltip>
      </div>
      <div>
        <b-tooltip
          position="is-bottom"
          :label="$t('Layout')"
          id="tree-dropdown"
        >
          <b-dropdown
            position="is-bottom-right"
            :can-close="false"
            @active-change="
              (open) => {
                treeOpen = open
              }
            "
          >
            <template #trigger="{}">
              <b-button
                @click="fetchLayout"
                icon-left="format-list-bulleted-square"
                class="editor-button"
                id="layout-button"
              >
              </b-button>
            </template>
            <b-dropdown-item :custom="true">
              <LayoutTree
                ref="layoutTree"
                :initData="layout"
                :settings="settings"
                :isOpen="treeOpen"
              ></LayoutTree>
            </b-dropdown-item>
          </b-dropdown>
        </b-tooltip>
      </div>
    </div>
  </div>
</template>

<script>
import Editor from './editor.js'
import StructureTypeManager from './vueComponents/StructureTypeManager.vue'
import SettingsModal from './vueComponents/SettingsModal.vue'
import Modal from './vueComponents/Modal.vue'
import { Singleton as Settings } from './singletons/settings.js'
import { mapState } from 'vuex'
import 'he-tree-vue/dist/he-tree-vue.css'
import {
  Tree, // Base tree
  Fold,
  Check,
  Draggable, // plugins
  foldAll,
  unfoldAll,
  cloneTreeData,
  walkTreeData,
  getPureTreeData, // utils
} from 'he-tree-vue'
import LayoutTree from './vueComponents/LayoutTree.vue'

// Vue.use(VueTreeList)

var that = this

import {
  OpenSeadragon,
  fabric,
  initOverlay,
} from 'openseadragon-fabricjs-overlay-jk'

//import OpenSeadragon from 'openseadragon'
//const OpenSeadragon = require('openseadragon')

export default {
  data() {
    return {
      treeOpen: false,
      canvas: {},
      layout: [],
      overlay: {},
      editor: {},
      editorState: null,
      Editor,
      vueFunctions: {},
      windowWidth: null,
      canvasHeight: null,
      leftClickTracker: null,
      drag: null,
      dragType: null,
      treeData: [
        { text: 'node 1' },
        { text: 'node 2', children: [{ text: 'node 2-1' }] },
      ],
      settings: {},
      // data: new Tree([]),

      instructions: [
        {
          string: this.$t(
            `New <strong>points</strong> can be added by clicking in the middle of a line and dragging the new point to a new position`
          ),
        },
        {
          string: this.$t(
            `Edit Shapes using the <strong>context menu</strong> (right click)`
          ),
        },
        {
          string: this.$t(
            `Hold <div class='description-icon' id='shift-icon'></div> to <strong>move</strong> and <strong>scale</strong> shapes`
          ),
        },
        {
          string: this.$t(
            `Press <div class='description-icon' id='ctrl-icon'></div> + L to select all lines of a selected region`
          ),
        },
        {
          string: this.$t(
            `Hold <div class='description-icon' id='ctrl-icon'></div> or <div class='description-icon' id='cmd-icon'></div> to select <strong>multiple</strong> shapes`
          ),
        },
        {
          string: this.$t(
            `Press <div class='description-icon' id='tab-icon' ></div> to select the <strong>next</strong> shape (following the reading order)`
          ),
        },
      ],
      options: {
        callback(isFullscreen) {
          that.$store.dispatch('detail/content/update', {
            fullScreen: isFullscreen,
          })
        },
      },
    }
  },
  components: { Tree: Tree.mixPlugins([Fold, Draggable]), LayoutTree },
  computed: {
    ...mapState('detail/content', ['fullScreen']),
  },
  watch: {
    structureTypes: {
      deep: true,
      handler(newTypes) {
        Settings.instance().structureTypes = newTypes
      },
    },
  },
  props: {
    pageXml: {
      key: String,
    },
    tileSource: {
      key: Object,
    },
    structureTypes: {
      key: Array,
    },
  },

  created() {
    let width = this.tileSource.width
    let viewerWidth = window.innerWidth
    this.scaleFactor = viewerWidth / width
    const rounded = (x) => Math.round(x * 1000000) / 1000000
    this.scaleFactor = rounded(this.scaleFactor)
    this.windowWidth = window.innerWidth
  },
  mounted() {
    const showLog = true
    const showWarn = true
    const showProtocolLog = true
    if (showLog) {
      window.log = console.log.bind(window.console)
    } else {
      window.log = () => {}
    }
    if (showProtocolLog) {
      window.protocolLog = console.log.bind(window.console)
    } else {
      window.protocolLog = () => {}
    }
    if (showWarn) {
      window.warn = console.warn.bind(window.console)
    } else {
      window.warn = () => {}
    }

    that = this
    let viewer = OpenSeadragon({
      id: 'osd',
      prefixUrl: 'openseadragon/images/',
      tabIndex: '',
      tileSources: [
        {
          tileSource: this.tileSource,
        },
      ],
      controlsFadeDelay: 60000,
      showZoomControl: false,
      showHomeControl: false,
      showFullPageControl: false,
      minZoomImageRatio: 0.5,
      maxZoomPixelRatio: 10,
      visibilityRatio: 0,
    })

    viewer.addOnceHandler('open', () => {
      this.fitToWidth()
      // console.log('viewer opened')
      this.$emit('hasLoaded')
    })

    initOverlay(OpenSeadragon, fabric)
    let overlay = viewer.fabricjsOverlay({
      scale: 1000,
    })
    let canvas = overlay.fabricCanvas()
    //this.canvas.renderOnAddRemove = false;
    canvas.uniformScaling = false
    canvas.fireRightClick = true
    // canvas.preserveObjectStacking = true
    canvas.uniScaleKey = 'null'
    canvas.centeredKey = 'null'
    canvas.selectionKey = 'null'
    canvas.altActionKey = 'null'
    canvas.altSelectionKey = 'null'

    let tileSourceRatio2 = this.tileSource.height / this.tileSource.width

    this.canvasHeight = canvas.width * tileSourceRatio2

    viewer.zoomPerClick = 1
    viewer.setControlsEnabled(false)
    this.vueFunctions = {
      setEditorState: this.setEditorState,
      save: this.save,
      setEdited: this.setEdited,
      openModal: this.openModal,
      getRouterBase: this.getRouterBase,
      getCanvasHeight: this.getCanvasHeight,
      getTileSourceWidth: () => this.tileSource.width,
      performAction: this.performAction,
      performSelection: this.performSelection,
      $t: this.$t.bind(this),
      setDrag: this.setDrag,
    }

    Settings.instance().on('update', (settings) => {
      this.saveSettingsToCookie(settings)
      this.settings = settings
    })
    if (this.structureTypes != null) {
      Settings.instance().structureTypes = this.structureTypes
    }
    const settings = this.getSettingsFromCookie()
    Settings.instance().set(settings)

    this.viewer = viewer
    window.layouteditor = new Editor(
      canvas,
      this.pageXml,
      this.scaleFactor,
      this.vueFunctions
    )
    window.layouteditor.render()
    let buffer = 0
    viewer.addHandler('canvas-drag', function () {
      if (buffer++ % 20 === 0) {
        if (!window.layouteditor.canvasDrag) {
          window.layouteditor.canvasDrag = true
        }
      }
    })
    this.fetchLayout()
  },

  beforeDestroy() {
    window.layouteditor.removeKeyListeners()
  },

  methods: {
    setDrag(enable) {
      // new OpenSeadragon.MouseTracker({
      //   element: this.viewer.canvas,
      //   pressHandler: () => this.viewer.setMouseNavEnabled(false),
      //   releaseHandler: () => this.viewer.setMouseNavEnabled(true),
      // })
      this.viewer.setMouseNavEnabled(enable)
      return

      if (this.dragStopper == null) {
        this.dragStopper = (event) => {
          event.preventDefaultAction = true
        }
      }
      if (enable) {
        this.viewer.removeHandler('canvas-drag', this.dragStopper)
      } else {
        this.viewer.addHandler('canvas-drag', this.dragStopper)
      }
    },
    getSettingsFromCookie() {
      if (this.$cookies == null) return
      return this.$cookies.get('transkribus-config')?.layoutSettings
    },
    saveSettingsToCookie(settings) {
      // console.log(settings)
      if (this.$cookies == null) return
      const config = this.$cookies.get('transkribus-config') || {}
      config.layoutSettings = { ...config?.layoutSettings, ...settings }
      this.$cookies.set('transkribus-config', JSON.stringify(config))
    },

    openSettingsModal() {
      this.$buefy.modal.open({
        parent: this,
        component: SettingsModal,
        props: {
          structureTypes: this.structureTypes,
        },
        events: {
          save: (structureTypes) => {
            this.$emit('publishStructureTypes', structureTypes)
          },
        },
      })
    },
    // handleTreeNodeClick(id) {
    //   const editor = window.layouteditor
    //   const shape = editor.getShape(id)
    //   editor.selectNewShape(shape)
    // },
    // setActiveShape(shape) {
    //   this.activeShapeId = shape?.id
    // },
    performAction(action) {
      this.$refs.layoutTree.performAction(action)
    },
    performSelection(selection) {
      this.$refs.layoutTree.performSelection(selection)
    },
    fetchLayout() {
      this.layout = window.layouteditor.getLayout()
    },

    // drop({ node, src, target }) {
    //   console.log('drop')
    //   console.log(node, src, target)
    // },
    // dropBefore({ node, src, target }) {
    //   console.log('drop-before')
    //   console.log(node, src, target)
    // },
    // dropAfter({ node, src, target }) {
    //   console.log('drop-after')
    //   console.log(node, src, target)
    // },
    setLeftClickDrag(activate) {
      // console.log(activate)
      // this.drag = null
      // if (this.leftClickTracker == null) {
      //   this.leftClickTracker = new OpenSeadragon.MouseTracker({
      //     element: this.viewer.container,
      //     nonPrimaryPressHandler: (event) => {
      //       if (event.button === 2) {
      //         // Middle
      //         // console.log('press')
      //         this.drag = {
      //           lastPos: event.position.clone(),
      //         }
      //       }
      //     },
      //     moveHandler: (event) => {
      //       if (this.drag) {
      //         // console.log('drag')
      //         var deltaPixels = this.drag.lastPos.minus(event.position)
      //         var deltaPoints =
      //           this.viewer.viewport.deltaPointsFromPixels(deltaPixels)
      //         this.viewer.viewport.panBy(deltaPoints)
      //         this.drag.lastPos = event.position.clone()
      //       }
      //     },
      //     nonPrimaryReleaseHandler: (event) => {
      //       if (event.button === 2) {
      //         // console.log('release')
      //         this.drag = null
      //       }
      //     },
      //   })
      // }
      // if (activate) {
      //   this.leftClickTracker.setTracking(true)
      //   console.log('called')
      //   return
      // }
      // this.leftClickTracker.setTracking(false)
    },

    openModal(heading, text) {
      this.$buefy.modal.open({
        parent: this,
        component: Modal,
        props: {
          heading: heading,
          text: text,
        },
      })
    },
    getRouterBase() {
      return this.$router?.options?.base || ''
    },
    getCanvasHeight() {
      return this.canvasHeight
    },
    buttonClick(editorButton) {
      window.layouteditor.buttonClick(editorButton)
    },
    setEditorState(state) {
      this.editorState = state
    },
    save() {
      const xml = window.layouteditor.createPageXML()
      //console.log(xml)
      this.$emit('publishXml', xml)
    },
    setEdited(boolean) {
      this.$emit('setEdited', boolean)
    },
    restartEditor(xml) {
      window.layouteditor.canvas.clear()
      window.layouteditor.removeCanvasListeners()
      window.layouteditor = new Editor(
        window.layouteditor.canvas,
        xml,
        this.scaleFactor,
        this.vueFunctions
      )
      window.layouteditor.render()
    },
    getButtonStyle(button) {
      if (
        ((this.editorState === Editor.STATE.EDIT ||
          this.editorState === Editor.STATE.MULTI_SELECT ||
          this.editorState === Editor.STATE.GROUP_SELECTION) &&
          button === Editor.BUTTON.POINTER) ||
        (this.editorState === Editor.STATE.ADD_BASELINES &&
          button === Editor.BUTTON.ADD_BASELINES) ||
        (this.editorState === Editor.STATE.ADD_REGIONS &&
          button === Editor.BUTTON.ADD_REGIONS) ||
        (this.editorState === Editor.STATE.ADD_TABLES &&
          button === Editor.BUTTON.ADD_TABLES) ||
        (this.editorState === Editor.STATE.ADD_RELATION &&
          button === Editor.BUTTON.ADD_RELATION)
      ) {
        return {
          'background-color': '	#C0C0C0',
        }
      }
      return {
        'background-color': 'white',
      }
    },
    setHome() {
      this.viewer.viewport.goHome()
    },
    fullscreen() {
      this.$fullscreen.toggle()
    },
    fitToWidth() {
      /*
       ** as seen on https://github.com/openseadragon/openseadragon/issues/839#issuecomment-181968800
       */
      const viewportBounds = this.viewer.viewport.getBounds()
      const viewportAspect = viewportBounds.height / viewportBounds.width
      const newBounds = this.viewer.world.getHomeBounds()
      newBounds.height = newBounds.width * viewportAspect
      this.viewer.viewport.fitBounds(newBounds, false)
    },
    zoomIn() {
      this.viewer.viewport.zoomBy(1.2)
    },
    zoomOut() {
      this.viewer.viewport.zoomBy(0.8)
    },
  },
}
</script>

