Editor.prototype.findShape = function (pointer) {
  if (this.settings.get('showWords')) {
    const foundShape = this.words().find((word) => {
      let polyCoords = word.getArea()
      let point = [pointer.x, pointer.y]
      return pointInPolygon(point, polyCoords)
    })
    if (foundShape) return foundShape
  }

  if (this.settings.get('showBaselines')) {
    const foundShape = this.baselines().find((baseline) => {
      let polyCoords = baseline.getArea()
      let point = [pointer.x, pointer.y]
      return pointInPolygon(point, polyCoords)
    })
    if (foundShape) return foundShape
  }

  if (this.settings.get('showRegions')) {
    const foundShape = this.getTextRegionsAndCells().find((region) => {
      let polyCoords = region.getArea()
      let point = [pointer.x, pointer.y]
      return pointInPolygon(point, polyCoords)
    })
    if (foundShape) return foundShape
  }
}

