Editor.prototype.merge = function () {
  if (this.activeShapes.areSiblings() === false) return
  let newShape
  if (this.activeShapes.areBaselines()) {
    newShape = this.mergeTextLines([...this.activeShapes])
  }
  if (this.activeShapes.areTextRegions()) {
    newShape = this.mergeTextRegions([...this.activeShapes])
  }
  const action = this.getMergeAction([...this.activeShapes], newShape)
  log(newShape)

  this.actionHandler.select({
    type: SELECTION_TYPE.CLEAR,
  })
  this.actionHandler.doAction(action)
  this.actionHandler.addAction(action)
  this.actionHandler.select({
    type: SELECTION_TYPE.ADD,
    ids: [newShape.id],
  })
}

Editor.prototype.mergeTextLines = function (textLines) {
  const merge = (lines) => {
    if (lines.length === 1) {
      return lines[0]
    }
    let newLine = new Baseline(this.canvas)
    let [line1, line2] = getClosestLine(lines)
    newLine.vertices = line1.vertices
      .map((v) => newLine.getVertex(v.left, v.top, v.id))
      .concat(line2.vertices.map((v) => newLine.getVertex(v.left, v.top, v.id)))
    newLine.words = line1.words.concat(line2.words)
    newLine.text = `${line1.text} ${line2.text}`
    return merge(
      [newLine].concat(
        lines.filter((line) => line.id != line1.id && line.id != line2.id)
      )
    )
  }
  // let parentRegion = this.getBaselineParent(textLines[0].id)
  let newLine = merge(textLines)

  // textLines.map((line) => {
  //   this.removeShape(line.id)
  // })
  // this.addBaseline(
  //   newLine,
  //   parentRegion.baselines.indexOf(textLines[0]),
  //   parentRegion.id
  // )
  return newLine
}

Editor.prototype.mergeTextRegions = function (textRegions) {
  let newRegion = new Region(this.canvas)

  let allRegionPoints = textRegions.reduce((allPoints, region) => {
    return allPoints.concat(region.vertices.map((v) => [v.left, v.top]))
  }, [])

  let allTextLines = textRegions.reduce((allLines, region) => {
    return allLines.concat(region.baselines)
  }, [])

  let hull = polygonHull(allRegionPoints)

  let newVertices = hull.map((point) => newRegion.getVertex(point[0], point[1]))

  newRegion.baselines = allTextLines

  // textRegions.map((region) => {
  //   this.removeShape(region.id)
  // })

  newRegion.addMultiplePoints(newVertices)
  // this.addRegion(newRegion, this.regions.indexOf(textRegions[0]))

  return newRegion
}

