Editor.prototype.getCursorUrl = function (cursor) {
  switch (cursor) {
    case Editor.CURSOR.SELECTION:
      return `url("${this.vueFunctions.getRouterBase()}images/Selection-Tool.png") 5 3, default`
    case Editor.CURSOR.TEXTLINE:
      return `url("${this.vueFunctions.getRouterBase()}images/Pen-Tool.png") 2 2, default`
    case Editor.CURSOR.TEXTREGION:
      return `url("${this.vueFunctions.getRouterBase()}images/Region-Tool.png") 13 13, default`
    case Editor.CURSOR.TABLE:
      return `url("${this.vueFunctions.getRouterBase()}images/Table-Tool.png") 15 15, default`
  }
}

