    //   if (event.target?.id == null) break
    //   const cutAfterId = (ids, cut_id) => {
    //     const newIds = []
    //     let found = false
    //     ids.map((id) => {
    //       if (found === false) {
    //         newIds.push(id)
    //       }
    //       if (id === cut_id) {
    //         found = true
