var Singleton = (function () {
  var instance

  function createInstance() {
    var object = new IdHandler()
    return object
  }

  return {
    instance: function () {
      if (!instance) {
        instance = createInstance()
      }
      return instance
    },
  }
})()

function IdHandler() {
  this.idCounter = 100
  this.ids = []
}

IdHandler.prototype.get = function (id = '') {
  // const newId = this.generateNew(prefix)
  // this.ids.push(newId)
  // protocolLog(`%cid registered: ${newId}`, 'color: aqua')
  // return newId
  this.requestId(id)
}

IdHandler.prototype.extendId = function (id) {
  this.idCounter++
  const newId = `${id}_${this.idCounter}`
  if (this.ids.includes(newId)) {
    return this.extendId(id)
  }
  this.ids.push(newId)
  return newId
}

// IdHandler.prototype.register = function (id) {
//   protocolLog(`%cid registered: ${id}`, 'color: aqua')
//   if (this.ids.includes(id)) {
//     warn('Error: Id already exists')
//   } else {
//     this.ids.push(id)
//   }
// }

IdHandler.prototype.requestId = function (id) {
  //protocolLog(`%cid requested: ${id}`, 'color: aqua')
  if (!this.ids.includes(id)) {
    this.ids.push(id)
    return id
  }
  const newId = this.extendId(id)
  //protocolLog(`%cid extended to: ${newId}`, 'color: aliceblue')
  return newId
}

export { Singleton }
