<template>
  <div class="modal-card" style="width: auto" v-if="types">
    <header class="modal-card-head">
      <p class="modal-card-title">
        <b-icon icon="label-multiple"></b-icon> {{ $t('Structure Types') }}
      </p>
      <button type="button" class="delete" @click="$emit('close')" />
    </header>
    <section class="modal-card-body">
      <b-collapse
        class="card"
        animation="slide"
        v-for="(type, index) of types"
        :key="index"
        :open="false"
      >
        <template #trigger="props">
          <div class="card-header" role="button">
            <p v-if="type.name" class="card-header-title">
              {{ type.name }}
            </p>
            <p v-else class="card-header-title">
              {{ type.label }}
            </p>
            <a class="card-header-icon">
              <span :style="backgroundDot(type)" class="dot"></span>
              <b-icon :icon="props.open ? 'menu-down' : 'menu-up'"> </b-icon>
            </a>
          </div>
        </template>
        <div class="card-content">
          <div class="content">
            <b-field label="Name">
              <b-input
                v-model.trim="type.name"
                @input="format(index)"
                :disabled="type.default === 'true'"
              ></b-input>
            </b-field>
            <b-field :label="$t('Choose color')">
              <v-swatches
                v-if="swatchesExists()"
                v-model="type.color"
                fallback-input-type="color"
                show-fallback
                :disabled="type.default === 'true'"
              ></v-swatches>
            </b-field>
            <b-button
              icon-left="minus"
              type="is-danger"
              @click="openConfirm(index)"
              expanded
              :disabled="type.default === 'true'"
              >{{ $t('Remove') }}</b-button
            >
          </div>
        </div>
      </b-collapse>
      <b-field>
        <b-button
          icon-left="plus"
          type="is-primary"
          @click="openAdd"
          expanded
          >{{ $t('Add new structure type') }}</b-button
        >
      </b-field>
      <b-field>
        <b-button
          icon-left="content-save-all"
          type="is-success"
          @click="saveConfig"
          expanded
          >{{ $t('Save') }}</b-button
        >
      </b-field>
    </section>
  </div>
</template>

<script>
import { mapState } from 'vuex'

import AddStructureType from './AddStructureType.vue'
import 'vue-swatches/dist/vue-swatches.css'

export default {
  props: {
    initTypes: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      types: JSON.parse(JSON.stringify(this.initTypes)),
    }
  },
  mounted() {
    //console.log(this.$router)
  },

  methods: {
    swatchesExists() {
      return this.$router != null
    },
    // isDefault(type) {
    //   return this.defaultConfig.definitions.structure.some(
    //     (t) => t.name === type.name
    //   )
    // },

    openConfirm(index) {
      this.$buefy.dialog.confirm({
        title: this.$t('Delete structure type'),
        message: this.$t(
          'Are you sure you want to delete this type definition?'
        ),
        confirmText: this.$t('Delete structure type'),
        type: 'is-danger',
        hasIcon: true,
        onConfirm: () => this.removeTag(index),
      })
    },
    openAdd() {
      const modal = this.$buefy.modal.open({
        parent: this,
        component: AddStructureType,
        props: {
          currentTypes: this.types,
        },
        events: {
          closeModal: (newType) => {
            this.types.push(newType)
            modal.close()
          },
        },
      })
    },
    backgroundDot(tag) {
      return 'background-color: ' + tag.color
    },
    removeTag(index) {
      this.types.splice(index, 1)
    },
    async saveConfig() {
      this.$emit('save', this.types)
    },
    format(index) {
      this.types[index].name = this.types[index].name.replaceAll(' ', '-')
    },
    openToast(message, type) {
      this.$buefy.toast.open({
        duration: 4000,
        message,
        position: 'is-top',
        type,
      })
    },
  },
}
</script>

<style scoped>
.dot {
  height: 25px;
  width: 25px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
}
.tags:not(:last-child) {
  margin-bottom: 0rem;
}
