Baseline.prototype.getGroup = function () {
  let vertices = this.vertices.map((v) =>
    this.myFabric.clone(v).set('opacity', 0.5)
  )
  let arrows = this.arrows.map((a) =>
    this.myFabric.clone(a).set('opacity', 0.5)
  )

  let polygon = this.myFabric.clone(this.polygon)
  let line = this.myFabric.clone(this.line).set('opacity', 0.5)
  let members = vertices.concat(arrows).concat(polygon).concat(line)

  return new fabric.Group(members, {
    id: this.id,
    hasControls: true,
    cornerStyle: 'circle',
    padding: 0,
    cornerSize: 8,
    cornerColor: 'transparent',
    transparentCorners: true,
    cornerStrokeColor: 'blue',
    lockSkewingX: true,
    lockSkewingY: true,
  }).setControlsVisibility({
    mtr: false,
  })
}

Baseline.prototype.showRed = function () {
  if (this.polygon.fill === this.polygon.defaultColor) {
    this.polygon.set('fill', this.polygon.deleteColor)
    this.canvas.requestRenderAll()
  }
}

Baseline.prototype.showNormal = function () {
  if (this.polygon.fill === this.polygon.deleteColor) {
    this.polygon.set('fill', this.polygon.defaultColor)
    this.canvas.requestRenderAll()
  }
}

