function Table(canvas, blueprint) {
  Shape.call(this, canvas)
  this.id = IdHandler.instance().requestId('t')

  this.state = Shape.STATE.EDIT

  this.prev = true
  this.hoverLine = null
  this.hoveredVertex = null
  this.mousePosition = {}

  this.selected = false

  //this.id = IdHandler.instance().get()
  this.idHandler = IdHandler.instance()
  this.canvas = canvas
  this.myFabric = MyFabric.instance()

  this.markedCell = null
  this.background = {}
  this.vertices = []
  this.horizontalLines = []
  this.verticalLines = []
  this.cellMatrix = new CellMatrix(0, 0)

  if (blueprint) {
    this.attributes = blueprint.attributes
    this.elements = blueprint.elements
    this.id = IdHandler.instance().requestId(blueprint.attributes.id)

    this.text = blueprint.elements.text
    let cells = blueprint.elements.cells.map((blueprint) =>
      this.createCell(blueprint)
    )
    if (!this.validateCells(cells)) {
      this.setTableInvalid(cells)
      return
    }
    cells = cells.reduce((acc, cell) => [...acc, ...this.requestChop(cell)], [])
    let numberOfRows = cells.reduce(
      (number, cell) => (cell.row >= number ? cell.row + 1 : number),
      0
    )
    let numberOfCols = cells.reduce(
      (number, cell) => (cell.col >= number ? cell.col + 1 : number),
      0
    )
    this.cellMatrix = new CellMatrix(numberOfRows, numberOfCols)
    cells.map((cell) => {
      let vertices = cell.elements.coords.map(
        (p) => this.getVertexByCoords(p.x, p.y) || this.creatVertex(p.x, p.y)
      )
      cell.setVertices(vertices)
      this.cellMatrix.setCell(cell, cell.row, cell.col)
    })
    this.cellMatrix.log()
    this.update()
    this.unSelect()
  }
}

Table.prototype = Object.create(Shape.prototype)
Table.prototype.constructor = Table

Table.prototype.getBlueprint = function (myIndex, keepId) {
  if (!this.attributes || !this.elements) {
    this.elements = {
      coords: {},
      textLines: [],
      text: '',
    }
  }
  //this.attributes.id = keepId ? this.id : `t${myIndex + 1}`
  this.attributes.id = this.id
  this.setReadingOrder(myIndex)
  this.elements.coords = this.getTableCoords()
  this.elements.cells = this.allCells()
    .filter((c) => !c.isSpanChild())
    .map((cell, index) => cell.getBlueprint(index, this.attributes.id, keepId))
  this.elements.text = this.text

  return { type: 'table', attributes: this.attributes, elements: this.elements }
}

Table.prototype.getType = function () {
  return Shape.TYPE.TABLE
}

Table.prototype.getLayout = function () {
  return {
    type: 'Table',
    id: this.id,
    children: this.allCells()
      .filter((c) => !c.isSpanChild())
      .map((c) => c.getLayout()),
    structureType: this.getStructureType(),
  }
}

Table.prototype.getTableCoords = function () {
  const maxRowIndex = this.cellMatrix.getHeight() - 1
  const maxColIndex = this.cellMatrix.getWidth() - 1
  const topLeft = this.cellMatrix.getCell(0, 0).vertices[0]
  const topRight = this.cellMatrix.getCell(0, maxColIndex).vertices[3]
  const bottomRight = this.cellMatrix.getCell(maxRowIndex, maxColIndex)
    .vertices[2]
  const bottomLeft = this.cellMatrix.getCell(maxRowIndex, 0).vertices[1]
  return [topLeft, topRight, bottomRight, bottomLeft].map(({ left, top }) => ({
    x: left,
    y: top,
  }))
}

Table.prototype.requestChop = function (cell) {
  let row = cell.row
  let col = cell.col
  let colSpan = cell.colSpan
  let coords = this.getCorrectedCoords(cell)
  let tubes = this.getTubes(coords, colSpan)
  let cells = tubes.reduce((acc, tube, index) => {
    return [...acc, ...this.getCellsOfTube(tube, row + index, col)]
  }, [])
  let newParentCoords = cells[0].elements.coords
  cells[0] = cell
  cells[0].elements.coords = newParentCoords
  return cells
}

Table.prototype.validateCells = function (cells) {
  const positions = []
  const maxRow = cells.reduce((acc, c) => (c.row > acc ? c.row : acc), 0)
  const maxCol = cells.reduce((acc, c) => (c.col > acc ? c.col : acc), 0)
  const rowSteps = Array.from({ length: maxRow + 1 }, (_, i) => i)
  const colSteps = Array.from({ length: maxCol + 1 }, (_, i) => i)
  const expectedPositions = rowSteps.reduce(
    (acc, rStep) => [...acc, ...colSteps.map((cStep) => [rStep, cStep])],
    []
  )
  const allCellsValid = cells.reduce((acc, cell) => {
    const colSpan = cell.colSpan
    const rowSpan = cell.rowSpan
    const rowSteps = Array.from({ length: rowSpan }, (_, i) => i)
    const colSteps = Array.from({ length: colSpan }, (_, i) => i)
    rowSteps.map((rStep) => {
      colSteps.map((cStep) => {
        positions.push([cell.row + rStep, cell.col + cStep])
      })
    })
    const coords = cell.elements.coords
    const cornerPoints = cell.elements.cornerPoints
    const expectedCoordsLength = rowSpan * 2 + colSpan * 2
    const validCornerpoints =
      JSON.stringify([0, rowSpan, rowSpan + colSpan, rowSpan * 2 + colSpan]) ===
      JSON.stringify(cornerPoints)
    const validCoordsLength = expectedCoordsLength === coords.length
    if (validCoordsLength && validCornerpoints) return acc
    if (expectedCoordsLength === 4 && coords.length > 4) return acc
    //console.log('Table Error: Invalid Cell found')
    ModalHandler.instance().open('Table Error', 'Invalid cell found')
    return false
  }, true)
  const validPositions = expectedPositions.every((ep) => {
    if (positions.some((p) => isEqual(ep, p))) return true
    //console.log(`Table Error: Missing cell at row: ${ep[0]} col: ${ep[1]}`)
    ModalHandler.instance().open(
      'Table Error',
      `Missing cell at row: ${ep[0]} col: ${ep[1]}`
    )
    return false
  })
  return allCellsValid && validPositions
}

Table.prototype.getCorrectedCoords = function (cell) {
  const notSpanning = cell.rowSpan * 2 + cell.colSpan * 2 === 4
  if (notSpanning && cell.elements.coords.length > 4) {
    return cell.elements.coords.filter((_, index) =>
      cell.elements.cornerPoints.includes(index)
    )
  }
  return cell.elements.coords
}

Table.prototype.getTubes = function (coords, colSpan) {
  let singleTube = colSpan * 2 + 2 === coords.length
  if (singleTube) return [coords]

  let coordsLength = coords.length

  let [leftFirst, leftSecond] = coords
  let lowerRest = coords.filter(
    (_, index) => index > 1 && index <= coordsLength - colSpan - 2
  )
  let [rightFirst, rightSecond] = coords.filter(
    (_, index) =>
      index > coordsLength - colSpan - 2 && index <= coordsLength - colSpan
  )
  let upperMidCoords = [
    ...coords.filter((_, index) => index > coordsLength - colSpan),
  ]

  let height = leftSecond.y - leftFirst.y
  let lowerMidCoords = upperMidCoords
    .map(({ x, y }) => ({ x: x, y: y + height }))
    .reverse()

  let tube = [
    leftFirst,
    leftSecond,
    ...lowerMidCoords,
    ...[rightFirst, rightSecond],
    ...upperMidCoords,
  ]
  let nextCoords = [
    leftSecond,
    ...lowerRest,
    rightFirst,
    ...[...lowerMidCoords].reverse(),
  ]
  return [tube, ...this.getTubes(nextCoords, colSpan)]
}

Table.prototype.getCellsOfTube = function (tube, row, col) {
  let newCells = []

  const popLast = (coords) => {
    let startIndex = coords.length / 2 - 1
    let newCell = this.createCell()
    newCell.col = col + startIndex - 1
    newCell.row = row
    newCell.elements = {
      coords: [
        coords[(startIndex + 3) % coords.length],
        coords[startIndex],
        coords[startIndex + 1],
        coords[startIndex + 2],
      ],
    }
    newCells = [newCell, ...newCells]
    let copy = [...coords]
    copy.splice(startIndex + 1, 2)
    if (copy.length > 2) {
      popLast(copy)
    }
  }
  popLast(tube)
  return newCells
}

Table.prototype.init = function (coords) {
  this.cellMatrix = new CellMatrix(1, 1)
  let cell = this.createCell()
  let vertices = coords.map((p) => this.creatVertex(p.x, p.y))
  cell.setVertices(vertices)
  this.cellMatrix.setCell(cell, 0, 0)
  this.cellMatrix.log()
  this.update()
}

Table.prototype.update = function () {
  if (!this.isVisible()) {
    this.removeFromCanvas()
    return
  }
  this.updateVertexSize()
  this.updateStructureText()
  this.updateReadingOrderText()
  this.allCells().map((cell) => {
    cell.tableSelected = this.selected
  })
  this.updateMatrix()
  this.updateLines()
  this.updateCells()
  if (!this.selected) {
    this.unSelect()
  }
}

Table.prototype.allCells = function () {
  return this.cellMatrix.allCells()
}

Table.prototype.updateMatrix = function () {
  this.cellMatrix.updateCellProperties()
}

