Table.prototype.updateLines = function () {
  let horizontalLines = this.cellMatrix
    .getHorizontalCircles()
    .map((circleLine, index) => {
      return {
        id: this.idHandler.requestId('tl'),
        polygons: calcLines(circleLine)
          .map(({ polygon }) => polygon)
          .slice(0, -1),
        vertices: [...circleLine],
        isVertical: false,
        index: index,
      }
    })
  let verticalLines = this.cellMatrix
    .getVerticalCircles()
    .map((circleLine, index) => {
      return {
        id: this.idHandler.requestId('tl'),
        polygons: calcLines(circleLine)
          .map(({ polygon }) => polygon)
          .slice(0, -1),
        vertices: [...circleLine],
        isVertical: true,
        index: index,
      }
    })
  this.lines = [...horizontalLines, ...verticalLines]
}

Table.prototype.updateCells = function () {
  //why?
  // this.cellMatrix.allCells().map((c) => c.removeFromCanvas())

  this.cellMatrix.allCells().map((c) => c.update())
}

