MyFabric.prototype.getHoverLine = function (
  vertices,
  id = this.idHandler.requestId('hl'),
  vertical
) {
  return new fabric.Polyline(
    vertices.map((c) => ({ x: c.left, y: c.top })),
    {
      id: id,
      strokeWidth: 3,
      originX: 'center',
      originY: 'center',
      stroke: 'black',
      padding: 20,
      fill: 'transparent',
      opacity: 0.6,
      hasControls: false,
      hasBorders: false,
      lockMovementX: !vertical,
      lockMovementY: vertical,
      evented: true,
      hoverCursor: vertical ? 'col-resize' : 'row-resize',
      vertices: vertices,
    }
  )
}

MyFabric.prototype.getCellBackground = function (points, color, opacity) {
  return new fabric.Polyline(points, {
    strokeWidth: 0,
    fill: color,
    opacity: opacity,
    selectable: false,
    evented: false,
  })
}

MyFabric.prototype.getCellFrame = function (points) {
  points.push(points[0])
  return new fabric.Polyline(points, {
    strokeWidth: 1.5,
    opacity: 0.8,
    stroke: 'orange',
    fill: 'transparent',
    selectable: false,
    evented: false,
  })
}

MyFabric.prototype.getStructureText = function (type, vertices, shapeType) {
  const polygon = vertices.map(({ left, top }) => [left, top])
  const color = this.settings.structureTypes.find((t) => t.name === type)?.color

  if (polygon.length === 2) {
    const newPoint = [polygon[0][0] + 0.1, polygon[0][1] + 0.1]
    polygon.push(newPoint)
  }
  const topLeft = this.getTopLeft(vertices)

  const size = this.settings.get('structureTypeSize')
  const left = topLeft.left + Math.sqrt(size) * this.scaleFactor
  const top = topLeft.top + Math.sqrt(size) * this.scaleFactor

  if (type == null) return

  return new fabric.Text(type, {
    originX: 'left',
    originY: 'top',
    fontSize: size,
    fill: color,
    // backgroundColor:
    //   shapeType === 'Baseline'
    //     ? 'rgba(0, 0, 255, 0.2)'
    //     : shapeType === 'Region'
    //     ? 'rgba(0, 128, 0, 0.2)'
    //     : shapeType === 'Table'
    //     ? 'rgba(249, 180, 45, 0.2)'
    //     : 'rgba(255,255, 0, 0.2)',
    backgroundColor: 'rgba(255,255,255,0.8)',
    evented: false,
    // left: topLeft[0][0],
    // top: topLeft[0][1] - 30 * this.scaleFactor,
    left,
    top,
  })
}

MyFabric.prototype.getROText = function (roIndex, vertices, shapeType) {
  const polygon = vertices.map(({ left, top }) => [left, top])

  if (polygon.length === 2) {
    const newPoint = [polygon[0][0] + 0.1, polygon[0][1] + 0.1]
    polygon.push(newPoint)
  }
  //const topLeft = polygonBounds(polygon)

  const topLeft = this.getTopLeft(vertices)

  const size = this.settings.get('structureTypeSize')
  const left = topLeft.left - Math.sqrt(size) * this.scaleFactor
  const top = topLeft.top + Math.sqrt(size) * this.scaleFactor

  if (roIndex == null) return

  const indexInt = parseInt(roIndex) + 1

  return new fabric.Text(indexInt.toString(), {
    originX: 'right',
    originY: 'top',
    fontSize: size,
    fill: 'black',
    backgroundColor: 'white',
    // backgroundColor:
    //   shapeType === 'Baseline'
    //     ? 'rgba(0, 0, 255, 0.2)'
    //     : shapeType === 'Region'
    //     ? 'rgba(0, 128, 0, 0.2)'
    //     : 'rgba(249, 180, 45, 0.2)',
    evented: false,
    //left: topLeft[0][0],
    //top: topLeft[0][1] - 30 * this.scaleFactor - 25,
    left,
    top,
  })
}

MyFabric.prototype.getTopLeft = function (vertices) {
  return vertices.reduce((acc, v) =>
    Math.abs(acc.left) + Math.abs(acc.top) > Math.abs(v.left) + Math.abs(v.top)
      ? v
      : acc
  )
}
