MyFabric.prototype.getArrow = function (
  x,
  y,
  angle,
  initId,
  size = 4,
  color = 'black',
  opacity = 0.4
) {
  return new fabric.Triangle({
    width: size,
    height: size,
    left: x,
    top: y,
    angle: angle + 90,
    fill: color,
    strokeWidth: 0.6,
    stroke: 'black',
    originX: 'center',
    originY: 'center',
    opacity,
    strokeWidth: 0.8,
    selectable: false,
    evented: false,
    id: initId,
  })
}

