MyFabric.prototype.getSplitLine = function (
  canvasDiagonal,
  mousePosition,
  angle
) {
  return new fabric.Line(
    [
      mousePosition.x - canvasDiagonal,
      mousePosition.y,
      mousePosition.x + canvasDiagonal,
      mousePosition.y,
    ],
    {
      strokeWidth: 1,
      fill: 'blue',
      stroke: 'blue',
      selectable: false,
      evented: false,
      originX: 'center',
      originY: 'center',
      angle,
    }
  )
}

