MyFabric.prototype.getPreviewLine = function (coords, opacity = 0.3) {
  return new fabric.Line(coords, {
    originX: 'center',
    originY: 'center',
    strokeWidth: 1,
    fill: 'blue',
    stroke: 'blue',
    opacity: opacity,
    selectable: false,
    evented: false,
  })
}

MyFabric.prototype.getPreviewRect = function (x1, y1, x2, y2) {
  let width = Math.abs(x1 - x2)
  let height = Math.abs(y1 - y2)
  let midPoint = lineMidpoint([
    [x1, y1],
    [x2, y2],
  ])
  return new fabric.Rect({
    left: midPoint[0],
    top: midPoint[1],
    originX: 'center',
    originY: 'center',
    width: width,
    height: height,
    strokeWidth: 1,
    stroke: 'black',
    fill: 'green',
    opacity: 0.3,
    selectable: false,
    evented: false,
  })
}

MyFabric.prototype.getSelectionRect = function (x, y) {
  const rect = new fabric.Rect({
    left: x,
    top: y,
    originX: 'center',
    originY: 'center',
    width: 0,
    height: 0,
    strokeWidth: 2,
    stroke: 'black',
    fill: 'transparent',
    opacity: 0.5,
    selectable: false,
    evented: false,
    initX: x,
    initY: y,
    updated: false,
  })
  rect.update = (x, y) => {
    rect.updated = true
    const width = Math.abs(rect.initX - x)
    const height = Math.abs(rect.initY - y)
    const midPoint = lineMidpoint([
      [rect.initX, rect.initY],
      [x, y],
    ])
    rect.set({ width, height, left: midPoint[0], top: midPoint[1] })
  }
  rect.getArea = () => {
    const halfWidth = rect.width / 2
    const halfHeight = rect.height / 2
    return [
      [rect.left - halfWidth, rect.top - halfHeight],
      [rect.left - halfWidth, rect.top + halfHeight],
      [rect.left + halfWidth, rect.top + halfHeight],
      [rect.left + halfWidth, rect.top - halfHeight],
    ]
  }
  return rect
}

