MyFabric.prototype.clone = function (object) {
  let objectClone
  object.clone((clone) => {
    objectClone = clone
  })
  return objectClone
}

MyFabric.prototype.getGroupData = function (group) {
  const getData = (group, left, top) => {
    let circles = group.getObjects().filter((o) => o.type === 'circle')
    let groups = group.getObjects().filter((o) => o.type === 'group')
    // let horizontalMid =

    //(group.left + group.width / 2) + g.left + (group.left + group.width / 2) + o.left

    //This is working

    // let verticalMid = top + group.height / 2
    return {
      vertices: circles.map((o) => ({
        left: left + (group.width / 2 + o.left) * group.scaleX,
        top: top + (group.height / 2 + o.top) * group.scaleY,
      })),
      groups: groups.map((g) =>
        getData(
          g,
          left + (group.width / 2 + g.left) * group.scaleX,
          top + (group.height / 2 + g.top) * group.scaleY
        )
      ),
    }
  }
  return getData(group, group.left, group.top)
}

MyFabric.prototype.getDock = function ([x, y], id, type) {
  const circleSize = this.settings.get('circleSize')
  return new fabric.Circle({
    radius: circleSize * 2,
    fill: 'transparent',
    strokeWidth: this.vertexRadius,
    stroke:
      type === 'baseline'
        ? 'blue'
        : type === 'region'
        ? 'green'
        : type === 'table'
        ? 'yellow'
        : 'orange',
    left: x,
    top: y,
    originX: 'center',
    originY: 'center',
    hasControls: false,
    hasBorders: false,
    selectable: false,
    id: id,
    defaultRadius: circleSize * 2,
  })
}

MyFabric.prototype.getRelationArrow = function (p1, p2, color = 'black') {
  let line = [p1, p2]
  let angle = lineAngle(line)
  let midPoint = lineInterpolate(line)(0.9)
  let arrow = this.getArrow(midPoint[0], midPoint[1], angle, -1, 20, color, 1)
  let arrowLine = new fabric.Line([...p1, ...midPoint], {
    strokeWidth: 1,
    stroke: color,
    selectable: false,
    evented: false,
  })

  const group = new fabric.Group([arrow, arrowLine], {
    selectable: false,
    evented: false,
  })
  return group
}

