import { DEFAULT_SETTINGS } from '../editor-config.js'
var Singleton = (function () {
  var instance

  function createInstance() {
    var object = new Settings()
    return object
  }

  return {
    instance: function () {
      if (!instance) {
        instance = createInstance()
      }
      return instance
    },
  }
})()

function Settings() {
  this.settings = { ...DEFAULT_SETTINGS }

  this.structureTypes = []

  this.eventHandlers = {
    update: [],
  }
}

Settings.prototype.set = function (settings) {
  // console.log(settings)
  if (settings == null) return
  this.settings = { ...this.settings, ...settings }
  protocolLog(`%csetting changed: %o`, 'color: purple', settings)
  this.emit('update', this.settings)
}

Settings.prototype.reset = function (key) {
  if (this.settings[key] == null) return
  this.settings[key] = DEFAULT_SETTINGS[key]
}

Settings.prototype.get = function (key) {
  if (key == null) return
  return this.settings[key]
}

Settings.prototype.getAll = function () {
  return { ...this.settings }
}

Settings.prototype.on = function (key, handler) {
  // console.log('register')
  this.eventHandlers[key].push(handler)
}

Settings.prototype.emit = function (key, event) {
  this.eventHandlers[key].map((h) => h(event))
}

export { Singleton }
